.menu
{
    position: fixed;
    top: 0;
    left: 0;

    display: flex;
    align-items: center;
    justify-content: center;

    width: 30%;
    height: 100vh;

    transition: .5s;
    transform: translateX(-100%);

    background-color: #537f81;
}

.menuActive
{
    transform: translateX(0%);
}

.active
{
    color: white;
}

.menuList
{
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;

    height: 50%;
}

.menuList a
{
    font-weight: 900;

    cursor: pointer;
    text-decoration: none;
    text-transform: uppercase;
}

.menuBtn
{
    position: absolute;
    top: 10px;
    right: -45px;

    display: inline-block;
}

.menuBtn:hover
{
    cursor: pointer;
}

.bar1,
.bar2,
.bar3
{
    width: 35px;
    height: 5px;
    margin: 6px 0;

    transition: .5s;

    background-color: #333;
}

.changeBar1
{
    -webkit-transform: rotate(-45deg) translate(-9px, 6px);
            transform: rotate(-45deg) translate(-9px, 6px);

    composes: bar1;
}

.changeBar2
{
    opacity: 0;

    composes: bar2;
}

.changeBar3
{
    -webkit-transform: rotate(45deg) translate(-8px, -8px);
            transform: rotate(45deg) translate(-8px, -8px);

    composes: bar3;
}
